.nav {
    grid-area: n;
    background-color: #3C4044;
    margin-top: 45px;
    margin-bottom: 5px;
    margin-right: 5px;
    height: 100%;
    width: 200px;
    text-align: center;
  }
  .item {
    margin: 10px;
    background-color: honeydew;
    padding: 5px;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
  }
  nav a{
    text-decoration: none;
    color: green;
    
  }
  nav .item:hover{
    background-color: aquamarine;
  }
  .item a.activeLink{
    color: rgb(19, 186, 236);
  }
   
  .friends{
    margin-top: 90px;
    height: 230px;
   
  }

  .friends h1{
    color: yellow;
    margin-bottom: 40px;
    border-top: 2px solid rgb(102, 121, 33); 
    border-bottom: 2px solid rgb(102, 121, 33); 
    
  }
  
  .friend{
    
    margin: 5px;
    font-size: 24px;
    text-align: start;
  }

  .friend img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    margin-right: 15px;
    margin-top: 4px;
    margin-left: 10px;
  
  }
  .friend span{
    position: absolute;
    margin-top: 5px;
    
  }
  .friend a{
    color: rgba(255, 255, 0, 0.616);
  }

  .friend:hover{
    background-color: teal;
  }